    .container-list {
        width: 50%;
        grid-gap: 24px;
    }
    
    .title {
        padding: 8px 16px;
        text-align: left;
        font-size: 20px;
        .name {
            font-size: 28px;
        }
    }
    
    .item {
        padding: 0 44px;
        width: 100%;
        .img {
            width: 1000px;
            img {
                height: 480px;
                width: 100%;
                margin-top: 16px;
            }
        }
        .tt-right {
            margin-top: 16px;
            text-align: right;
            .money {
                font-weight: bold;
                font-size: 24px;
            }
            .log {
                width: 200px;
                height: 50px;
                margin-top: 8px;
                padding: 8px 16px;
            }
        }
    }
    
    .tt {
        text-align: left;
        margin-top: 16px;
        .sumary {
            font-size: 20px;
            line-height: 2;
            // text-align: left;
        }
    }
    
    .img {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
    }
    
    //table
    .container-table {
        padding: 0 44px;
        width: 70%;
        /* align-items: center; */
        /* text-align: center; */
        margin-left: auto;
        margin-right: auto;
        h1 {
            padding: 16px;
        }
    }
    
    .container-btn {
        width: 50%;
        // grid-template-columns: repeat(2, 1fr);
        // grid-gap: 24px;
    }
    
    #customers {
        font-family: Arial, Helvetica, sans-serif;
        border-collapse: collapse;
        width: 100%;
    }
    
    #customers td,
    #customers th {
        border: 1px solid #ddd;
        padding: 8px;
    }
    
    #customers tr:nth-child(even) {
        background-color: #f2f2f2;
    }
    
    #customers tr:hover {
        background-color: #ddd;
    }
    
    #customers th {
        padding-top: 12px;
        padding-bottom: 12px;
        text-align: left;
        background-color: #04AA6D;
        color: white;
    }